import { login, logout, getInfo } from '@/api/login'
import { getToken, setToken, removeToken } from '@/utils/auth'

const user = {
  state: {
    token: getToken(),
    // name: 'djlun',
    // avatar: 'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=173886539,2588581153&fm=27&gp=0.jpg',
    name: '',
    avatar: '',
    roles: []
  },

  mutations: {
    SET_TOKEN: (state, token) => state.token = token,
    SET_NAME: (state, name) => state.name = name,
    SET_AVATAR: (state, avatar) => state.avatar = avatar,
    SET_ROLES: (state, roles) => state.roles = roles
  },

  actions: {
    // 
    Login: ({ commit }, userInfo) => {
      console.log('action')
      const username = userInfo.username.trim() // trim()
      return new Promise((resolve, reject) => {
        login(username, userInfo.password)
        .then(res => {
          console.log('' + JSON.stringify(res))
          const data = res.data
          setToken(data.token)
          commit('SET_TOKEN', data.token)
          resolve()
        })
        .catch(err => reject(err))
      })
    },

    // 
    GetInfo: ({ commit, state }) => {
      return new Promise((resolve, reject) => {
        getInfo(state.token)
        .then(res => {
          const data = res.data
          if (data.roles && data.roles.length > 0) {
            commit('SET_ROLES', data.roles)
          } else {
            reject('getInfo: roles must be a non-null array!')
          }
          commit('SET_NAME', data.name)
          commit('SET_AVATAR', data.avatar)
          resolve(res)
          // reject('token') // action
        })
        .catch(err => reject(err))
      })
    },

    // 
    LogOut: ({ commit, state }) => {
      console.log('actions')
      return new Promise((resolve, reject) => {
        logout(state.token)
        .then(() => {
          commit('SET_TOKEN', '')
          commit('SET_ROLES', [])
          removeToken()
          resolve()
        })
        .catch(err => reject(err))
      })
    },

    //  
    FedLogOut: ({ commit }) => {
      return new Promise(resolve => {
        commit('SET_TOKEN', '')
        removeToken()
        resolve()
      })
    },

    // 
    ChangeRoles: ({ commit, dispatch }, role) => {
      return new Promise(resolve => {
        // 
        // 
        // token
        // 
        commit('SET_TOKEN', role)
        setToken(role)
        getInfo(role) //  api 
        .then(res => {
          const data = res.data
          commit('SET_ROLES', data.roles)
          commit('SET_NAME', data.name)
          commit('SET_AVATAR', data.avatar)
          dispatch('GenerateRoutes', data) //  
          resolve()
        })
      })
    }
  }
}

export default user


