import router from './router'
import store from './store'
// import NProgress from 'nprogress' // Progress 
// import 'nprogress/nprogress.css'// Progress 
import { Message } from 'element-ui'
import { getToken/* , removeToken */ } from '@/utils/auth'
// import { curTimeStamp } from './utils'

// NProgress.configure({ showSpinner: false })

const hasPermission = (roles, permissionRoles) => {
  if (roles.includes('admin')) return true
  if (!permissionRoles) return true
  // try {
  console.log('permissionRoles', permissionRoles)
  return roles.some(role => permissionRoles.includes(role))
  // } catch (error) {
  //   Message.error('')
  // }
}

const whiteList = ['/login']  // 

router.beforeEach((to, from, next) => {
  // if (curTimeStamp() - getToken() > 10000) {
  //   removeToken()
  // }
  // NProgress.start() // start progress bar
  if (getToken()) {
    if (to.path === '/login') {
      next({ path: '/' })
      // NProgress.done() // if current page is dashboard will not trigger	afterEach hook, so manually handle it
    } else {
      if (store.getters.roles.length === 0) {
        store.dispatch('GetInfo') // 
        .then(res => {
          const roles = res.data.roles
          store.dispatch('GenerateRoutes', { roles }) // 
          .then(() => {
            router.addRoutes(store.getters.addRouters) // 
            next({ ...to, replace: true }) // hack addRoutes ,set the replace: true so the navigation will not leave a history record
          })
        })
        .catch((err) => {
          store.dispatch('FedLogOut')
          .then(() => {
            Message.error(err)
            next({ path: '/' })
          })
        })
      } else {
        // next()  
        // 
        // 
        if (hasPermission(store.getters.roles, to.meta.roles)) {
          next()
        } else {
          next({
            path: '/401',
            replace: true,
            query: {
              noGoBack: true
            }
          })
        }
        // next()
      }
    }
  } else {
    if (whiteList.includes(to.path)) {
      next()
    } else {
      next(`/login?redirect=${to.path}`) // 
      // NProgress.done() // if current page is login will not trigger afterEach hook, so manually handle it
    }
  }
})

// router.afterEach(() => {
//   NProgress.done() // finish progress bar
// })


