const Layout = () => import('@/views/layout')
const res = []
const data = [
  { path: 'reportforms', meta: { title: '', roles: ['pczg']}, children: [
    { path: 'executive', meta: { title: '', roles: ['pczg'] }, children: [
      { path: 'executive1', meta: { title: '', roles: ['pczg'] } },
      { path: 'executive2', meta: { title: '', roles: ['pczg'] } },
    ]},
    { path: 'research', meta: { title: '', roles: ['pczg'] }, children: [
      { path: 'research1', meta: { title: '', roles: ['pczg'] } },
      { path: 'research2', meta: { title: '', roles: ['pczg'] } },
    ]},
    { path: 'produce', meta: { title: '', roles: ['pczg'] }, children: [
      { path: 'produce1', meta: { title: '', roles: ['pczg'] } },
      { path: 'produce2', meta: { title: '', roles: ['pczg'] } },
      { path: 'produce3', meta: { title: '', roles: ['pczg'] } },
      { path: 'produce4', meta: { title: '', roles: ['pczg'] } },
      { path: 'produce5', meta: { title: '', roles: ['pczg'] } },
    ]},
    { path: 'sales', meta: { title: '', roles: ['pczg'] }, children: [
      { path: 'sales1', meta: { title: '', roles: ['pczg'] } },
      { path: 'sales2', meta: { title: '', roles: ['pczg'] } },
      { path: 'sales3', meta: { title: '', roles: ['pczg'] } },
      { path: 'sales4', meta: { title: 'PIP', roles: ['pczg'] } },
      { path: 'sales5', meta: { title: 'PIP', roles: ['pczg'] } },
    ]},
  ]},
  { path: 'dataentry', meta: { title: '', roles: ['djlun', 'pczg']}, children: [
    { path: 'datacheck', meta: { title: '', roles: ['pczg'] } },
    { path: 'dataimport', meta: { title: '', roles: ['pczg'] } },
  ]},
  { path: 'maindata', meta: { title: '', roles: ['djlun', 'pczg']}, children: [
    { path: 'maindatasearch', meta: { title: '', roles: ['pczg'] } },
    { path: 'maindatamanage', meta: { title: '', roles: ['pczg'] } },
  ]},
  { path: 'system', meta: { title: '', roles: ['djlun', 'pczg']}, children: [
    { path: 'systemmanage', meta: { title: '', roles: ['pczg'] } },
  ]}
]

data.forEach(el => {
  const children = []
  el.children.forEach(item => {
    if (item.children) {
      const child = []
      item.children.forEach(list => {
        child.push({
          path: list.path,
          component: () => import(`@/views/${el.path}/${item.path}/${list.path}`),
          name: list.path,
          meta: list.meta,
        })
      })
      children.push({
        path: item.path,
        component: () => import(`@/views/${el.path}/${item.path}/index`),
        name: item.path,
        meta: item.meta,
        children: child
      })
    } else {
      children.push({
        path: item.path,
        component: () => import(`@/views/${el.path}/${item.path}`),
        name: item.path,
        meta: item.meta
      })
    }
  })
  res.push({
    path: `/${el.path}`,
    component: Layout,
    meta: el.meta,
    children: children
  })
})

export default res

